
import { AjaxHttp } from '../../../js/ajaxHttp'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"
import PlateType from '../../../js/enum.js'
import { mapState } from 'vuex'
import event from '../../../js/Event.js'

export default {
name: 'taskhandle',
components: {
XInput,
XTextarea
},
computed: {
PlateType() {
return PlateType;
},
...mapState([
'detail',
'taskDetail'
])
},
data() {
return {
subject: '',
description: '',
priority: '普通', //高 #FE6369 普通 #3EBF8D 低 #F6A45C
prioritydata: [{ color: '#F6A45C', name: '低' }, { color: '#3EBF8D', name: '普通' }, { color: '#FE6369', name: '高' }],
module_id: '',
module_name: '',
about_role: [],
}
},
beforeDestroy() {
event.$off("saveDone");
},
beforeRouteLeave(to, from, next) {
if (to.name === 'relatelist' || to.name === 'selectlist') {
this.$store.commit('updateDetail', {
type: 'taskhandle',
subject: this.subject,
description: this.description,
priority: this.priority,
module_id: this.module_id,
module_name: this.module_name,
about_role: this.about_role,
});

event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
} else {
this.$store.commit('updateDetail', '');
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist') {
event.$off("selectedRelateItem");
}
next();
},
mounted() {
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));

if (this.detail !== '' && this.detail.type === 'taskhandle') {
this.subject = this.detail.subject;
this.description = this.detail.description;
this.priority = this.detail.priority;
this.module_id = this.detail.module_id;
this.module_name = this.detail.module_name;
this.about_role = this.detail.about_role;
}
},
methods: {
colorSelect(name) {
if (this.priority !== name) {
this.priority = name;
}
},
onRelateClick() {
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Customer,
fromtype: 'taskhandle',
action: 'select',
section: '-1',
row: '-1',
addition: '0'
}
});
},
saveInfos() {
if (this.subject === '') {
this.$vux.toast.text('请填写任务名称', 'top');
return;
}
var about_role_id = '';
for (var i = 0; i < this.about_role.length; i++) {
if (i === 0) {
about_role_id = ',' + this.about_role[i].role_id + ',';
} else {
about_role_id = about_role_id + this.about_role[i].role_id + ','
}
}
var postParams = {
subject: this.subject,
description: this.description,
priority: this.priority,
module: this.module_id === ''? '' :'customer',
module_id: this.module_id,
about_role_id: about_role_id,
}
var url = '';
if (this.$route.params.action === 'edit') {
url = 'm=task&a=edit';
postParams.id = this.$route.params.id;
} else if (this.$route.params.action === 'default') {
url = 'm=task&a=add';
postParams.type_id = this.$route.params.id;
}

AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
if (wself.$route.params.action === 'edit') {
wself.taskDetail.refresh = true;
wself.$store.commit('updateTaskDetail', wself.taskDetail);
}
wself.$router.go(-1);
}
});
},
handleSelectedInfo(data) { //相关列表数据处理
if (data.type === PlateType.Customer) {
this.detail.module_id = data.item.id;
this.detail.module_name = data.item.title;
this.$store.commit('updateDetail', this.detail);
} else if (data.type === '4') { //任务下分配人
this.detail.about_role = data.data;
this.$store.commit('updateDetail', this.detail);
}
},
enterSelectlist() {
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: 'taskhandle',
selecttype: '4',
section: '-1',
row: '-1',
}
});
}
}
}




